function my_func() {



  var homeAboutContentWrapper = document.getElementById("homeAboutContentWrapper");
  var editButton = document.getElementById("editButton");

  var preTagClass;
  if (localStorage.getItem("preTagClass")) {
    console.log("class  exists")
  }
  else {
    preTagClass = document.getElementById("preTagAboutContent").getAttribute("class");
    localStorage.setItem("preTagClass", preTagClass)
  }

  var adminMode = localStorage.getItem("adminMode");
  if (adminMode == 0) {

    var pass = localStorage.getItem("password");
    let entered = prompt("Please enter your password:");

    if (pass != entered) {
      return;
    }

    localStorage.setItem("adminMode", 1);
    editButton.innerText = "Done Editing";




    var ta = document.createElement("textarea");
    ta.innerHTML = localStorage.getItem("homeAboutContent");
    ta.setAttribute("id", "taTagAboutContent");

    ta.setAttribute("style", "background-color: #fff;color:black;width:100%;height:400px;white-space: pre-wrap;")


    var preTagAboutContent = document.getElementById("preTagAboutContent");
    console.log(preTagAboutContent.innerText);
    // var oldtext = preTagAboutContent.innerText;
    ta.innerText = localStorage.getItem("homeAboutContent");
    preTagAboutContent.remove();
    homeAboutContentWrapper.appendChild(ta);
  }
  else {
    localStorage.setItem("adminMode", 0);
    editButton.innerText = "Edit Content";




    var pre = document.createElement("pre");
    pre.innerHTML = localStorage.getItem("homeAboutContent");
    pre.setAttribute("id", "preTagAboutContent");
    pre.setAttribute("class", localStorage.getItem("preTagClass"));
    pre.setAttribute("style", "white-space: pre-wrap;");

    var taTagAboutContent = document.getElementById("taTagAboutContent");
    console.log(taTagAboutContent.value);
    var newtext = taTagAboutContent.value;
    localStorage.setItem("homeAboutContent", newtext);
    pre.innerHTML = localStorage.getItem("homeAboutContent")
    taTagAboutContent.remove();
    homeAboutContentWrapper.appendChild(pre);
  }


}

